<template>
  <div class="mt-10" style="height: calc(100% - 40px);">
		<v-card height="100%" class="mx-15 rounded-lg" color="element_background">
			<v-card-title class="background_color_second white--text">
				<v-row align="center" no-gutters>
					<v-col>
						<h2 style="font-weight: lighter;">Настройки профиля</h2>
					</v-col>
				</v-row>
			</v-card-title>

			<v-card-text class="pa-0" style="height: calc(100% - 64px);">
				 <v-container
					id="scroll-target"
					style="max-height: 400px"
					class="overflow-y-auto"
				>
					<v-row>
						<v-col>
							<h2>Основная информация</h2>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col>
							<v-text-field 
								class="mt-2 mx-3"
								label="Фамилия" 
								required
								:rules="[ requiredFieldRule ]"
								v-model="firstname"
							/>
						</v-col>
						<v-col>
							<v-text-field 
								class="mt-2 mx-3"
								label="Имя" 
								required
								:rules="[ requiredFieldRule ]"
								v-model="secondname"
							/>
						</v-col>
					</v-row>
					<v-row  no-gutters>
						<v-col>
							<v-text-field 
								class="mt-2 mx-3"
								label="Отчество" 
								required
								:rules="[ requiredFieldRule ]"
								v-model="surname"
							/>
						</v-col>
						<v-col>
							<v-text-field 
								v-if="userData.status != 'Тренер'"
								class="mt-2 mx-3"
								label="Номер студенческого билета"
								readonly
								v-model="studCard"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col>
							<template v-if="userData.status != 'Тренер'">
								<div class="mt-1">
									Группа:
								</div>
								<GroupSelectorBtn 
									:selected_group="usergroup"
									@changeGroup="changeGroup"
								/>
							</template>
						</v-col>
						<v-col>
							<div class="mt-1">
								Фото профиля:
							</div>
							<v-btn
								class="mt-2 white--text"
								color="menu_btn_1"
								@click="uploadPhoto"
							>
								Выбрать фото
							</v-btn>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-btn 
								color="green"
								class="white--text"
								:loading="dataSaveLoading"
								@click="updateUserData"
							>
								Сохранить изменения
							</v-btn>
						</v-col>
					</v-row>


					<v-divider class="my-5"/>
					

					<v-row>
						<v-col>
							<h2>Сменить пароль</h2>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col>
							<v-text-field 
								class="mt-2 mx-3"
								label="Прежний пароль" 
								required
								type="password"
								:rules="[ requiredFieldRule, passwordFieldRule ]"
								v-model="oldPassword"
							/>
						</v-col>
						<v-col>
							<v-text-field 
								class="mt-2 mx-3"
								label="Новый пароль" 
								required
								type="password"
								:rules="[ requiredFieldRule, passwordFieldRule ]"
								v-model="newPassword"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col>
							<v-text-field 
								class="mt-2 mx-3"
								label="Повторите новый пароль" 
								required
								type="password"
								:rules="[ 
									requiredFieldRule, 
									() => newPassword == repeatNewPassword || 'Пароли не совпадают'
								]"
								v-model="repeatNewPassword"
							/>
						</v-col>
						<v-col>
							<v-btn 
								color="green"
								class="white--text mt-4"
								:loading="passwordSaveLoading"
								@click="updateUserPassword"
							>
								Сменить пароль
							</v-btn>
						</v-col>
					</v-row>
				</v-container>
			</v-card-text>

		</v-card>
	</div>
</template>

<script>
import GroupSelectorBtn from '@/components/ui/GroupSelectorBtn.vue'
import {mapGetters} from 'vuex'

export default {
	data: () => ({
		dataSaveLoading: false,
		passwordSaveLoading: false,

		valid: false,

		firstname: '',
		secondname: '',
		surname: '',

		studCard: '',

		oldPassword: '',
		newPassword: '',
		repeatNewPassword: '',
		
		usergroup: '',

		requiredFieldRule: v => !!v || 'Поле обязательно',
		passwordFieldRule: v => ( v.length >= 8 && v.length <= 64 ) || 'Допустимая длина от 8 до 64 символов',
	}),

	methods: {
		changeGroup(new_group) {
			this.usergroup = new_group
		},
		uploadPhoto() {
			this.$store.commit('showSnackbarWarning', 'Функция находится в разработке')
		},
		async updateUserData() {
			this.dataSaveLoading = true
			await this.$store.dispatch(
				'pushJsonData',
				{
					endpoint: '/user/edit',
					body: {
						'firstname': this.firstname,
						'secondname': this.secondname,
						'surname': this.surname,
						'group': this.group,
					},
					method: 'PUT'
				}
			)
			this.$store.commit('showSnackbarSuccess', 'Данные успешно обновлены')
			this.dataSaveLoading = false
		},
		async updateUserPassword() {
			this.passwordSaveLoading = true
			try {
				await this.$store.dispatch(
					'pushJsonData', 
					{
						endpoint: '/user/edit-password', 
						body: {
							'old_password': this.oldPassword, 
							'new_password': this.newPassword
						}, 
						method: 'PUT',
						saveData: false,
					}
				)
				this.$store.commit('showSnackbarSuccess', 'Пароль успешно изменен')
				this.oldPassword = ''
				this.newPassword = ''
				this.repeatNewPassword = ''
			}
			catch {
				this.$store.commit('showSnackbarError', 'Неверный прежний пароль')
			}
			this.passwordSaveLoading = false
		},
	},

	computed:{
    ...mapGetters(['userData']),
  },

	components: { GroupSelectorBtn },

	mounted() {
		this.firstname = this.userData['firstname']
		this.secondname = this.userData['secondname']
		this.surname = this.userData['surname']
		this.usergroup = this.userData['group']
		this.studCard = this.userData['student_card']
	},
}
</script>

<style>

</style>